* {
  box-sizing: border-box;
  font-family: Karla, sans-serif;
}

body {
  margin: 0;
  background-color: #0b2434;
  padding: 20px;
}

main {
  background-color: #f5f5f5;
  margin-inline: auto;
  max-width: 800px;
  height: 400px;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.title {
  margin: 0;
}

.instructions {
  text-align: center;
  padding-inline: 30px;
}

.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 20px;
  margin-bottom: 40px;
}

.die {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 50px;

  /* *from course */
  /* background: #ffffff; */

  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 10px;

  cursor: pointer;
}

.die h2 {
  font-weight: 700;
  font-size: 20.5889px;
  line-height: 24px;
}

/* * my code */
/* .die.held {
  background-color: #59e391;
} */

.roll {
  /* margin-top: 3rem; */
  min-width: 100px;
  padding-inline: 20px;
  line-height: 3rem;
  border: none;
  background: #5035ff;
  font-weight: 700;
  font-size: 1.2rem;
  box-shadow: 0px 3.2px 7.68px rgba(0, 0, 0, 0.18);
  color: white;
  border-radius: 10px;
}

.roll:focus {
  outline: none;
}

.roll:active {
  box-shadow: 0px 3.2px 7.68px rgba(0, 0, 0, 0.18), inset 5px 5px 10px -3px rgba(0, 0, 0, 0.5);
}
